.map-group-check {
  display: inline-block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 18px;
  user-select: none;
  /*Hide the browser's default checkbox*/
  /* On mouse-over, add a grey background color */
  /* When the checkbox is checked, add a blue background */
  /* Show the checkmark when checked */
}

.map-group-check input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.map-group-check .checkra {
  border-radius: 14px;
}

.map-group-check .checkmark {
  position: absolute;
  top: 12px;
  left: 0;
  height: 20px;
  width: 20px;
  background: #eee;
  /* Create the checkmark/indicator (hidden when not checked) */
  /* Style the checkmark/indicator */
}

.map-group-check .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.map-group-check .checkmark:after {
  left: 5px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

.map-group-check:hover input ~ .checkmark {
  background-color: #ccc;
}

.map-group-check input:checked ~ .checkmark {
  background-color: #2196F3;
}

.map-group-check input:checked ~ .checkmark:after {
  display: block;
}

